import React , { useState , useEffect }  from "react"
import Layout from "../../components/layout"
import SEO from '../../components/seo'
import stl from '../../STYLES/USER_INFO/user_info.module.scss'
import { withUnAuth } from '../../components/ifAuth'
import Nationality from '../../components/SELECT_OPTIONS/nationality'
import States from '../../components/SELECT_OPTIONS/state'
import { EDIT_USER , GET_CURRENT_USER , GET_ALL_EMPLOYEES } from '../../queries/index'
import { useMutation} from '@apollo/react-hooks';
import { AlertSuccess , ErrorAlert } from '../../helpers/index'


const initialState = {
    _id:"",
    name:"",
    nationality:"",
    age:"",
    languages:[],
    aboutMe:"",
    skills:"",
    phone_number:"",
    state:"",
    city:"",
    hiring:false,
    working:false,
    available:false,

}

const EditProfileBody = withUnAuth(({ getCurrentUser }) => {

    const [ valState , setState ] = useState(initialState)


    useEffect(() => {
        const { name , characteristic , job , age , aboutMe , skills , phone_number , location , _id } = getCurrentUser
       setState({name: name ? name : '' ,
                 _id,
                 phone_number: phone_number ? phone_number : "",
                 skills: skills ? skills : "",
                 city:location.city ? location.city : "",
                 state:location.state ? location.state : "",
                 nationality:characteristic.nationality ? characteristic.nationality : ""  ,
                 languages:characteristic.languages,
                 age:age ? age : 0 , 
                 aboutMe: aboutMe ? aboutMe : "" ,
                 hiring:job.hiring,
                 working:job.working,
                 available:job.available
                })
    },[getCurrentUser])

    const setStateValues = e => {
        const { name , value } = e.target
        if(name === 'age'){
            return setState({...valState , age:Number(value) })
        }
        if(name === 'languages'){
            if(!valState.languages.includes(value)){
               return setState({...valState , languages:[...valState.languages , value]})
            }else{
                const languages = valState.languages.filter(val => val !== value)
                return setState({...valState , languages})
            }
        }
        if(name === 'hiring' || name === 'working' || name === 'available'){
            if(value === 'yes'){
                return setState({...valState , [name]:true})
            }else{
                return setState({...valState , [name]:false})
            }
        }
        setState({...valState , [name]:value})
    }

    const [editUser] = useMutation(EDIT_USER , {

       variables:{
        _id:valState._id,
        name:valState.name,
        nationality:valState.nationality,
        age:valState.age,
        languages:valState.languages,
        aboutMe:valState.aboutMe,
        skills:valState.skills,
        phone_number:valState.phone_number,
        state:valState.state,
        city:valState.city,
        hiring:valState.hiring,
        working:valState.working,
        available:valState.available
       },
       onError:(error) => ErrorAlert(error),
       onCompleted:({editUser}) => AlertSuccess(editUser.message),
       refetchQueries:() => [{query:GET_CURRENT_USER} , {query:GET_ALL_EMPLOYEES}]
    })

    const handleSubmit = async e => {
       e.preventDefault()
       await editUser()
    }


    const { name , age , phone_number , hiring , working , available , nationality , aboutMe , skills , state , city } = valState
    return (
        <div className = {stl.background}>
     <div className = {stl.wrapper}>
        <h1>EDIT PROFILE</h1>
        <form onSubmit = {(e) => handleSubmit(e)} >
        <div className = {stl.infoWrapper}>
          <h3>My Info</h3>
          
                <h4>Name:</h4>
                <input onChange = {e => setStateValues(e)} name = 'name' type = 'text' value = { name } />
                <h4>Nationality:</h4>
                <Nationality  name = 'nationality' style = {{marginBottom:"20px"}} value = {nationality} onChange = {e => setStateValues(e)} />
                <h4>Age: </h4>
                <input  onChange = {e => setStateValues(e)} name = 'age' value = {age}  min = '18' type = 'number' />
                <h4>Languages Speak: </h4>
                <div className = {stl.languages}>
                    <label htmlFor = 'english'>
                        <input checked = {valState.languages.includes('English')} onChange = {e => setStateValues(e)} id = 'english' name = 'languages' value = 'English' type = 'checkbox' /> English
                    </label>
                    <label htmlFor = 'russian'>
                        <input checked = {valState.languages.includes('Russian')} onChange = {e => setStateValues(e)} id = 'russian' name = 'languages' value = 'Russian' type = 'checkbox' /> Russian
                    </label>
                    <label htmlFor = 'spanish'>
                        <input checked = {valState.languages.includes('Spanish')} onChange = {e => setStateValues(e)} id = 'spanish' name = 'languages' value = 'Spanish' type = 'checkbox' /> Spanish
                    </label>
                    <label htmlFor = 'chinese'>
                        <input checked = {valState.languages.includes('Chinese')} onChange = {e => setStateValues(e)} id = 'chinese' name = 'languages' value = 'Chinese' type = 'checkbox' /> Chinese
                    </label>
                    <label htmlFor = 'german'>
                        <input checked = {valState.languages.includes('German')} onChange = {e => setStateValues(e)} id = 'german' name = 'languages' value = 'German' type = 'checkbox' /> German
                    </label>
                    <label htmlFor = 'french'>
                        <input  checked = {valState.languages.includes('French')} onChange = {e => setStateValues(e)} id = 'french' name = 'languages' value = 'French' type = 'checkbox' /> French
                    </label>
                </div>
                <h4>About me: </h4>
                <textarea onChange = {e => setStateValues(e)} value = {aboutMe} name = 'aboutMe' type = 'text'  />
                <h4>Skills \ Experience:</h4>
                <textarea onChange = {e => setStateValues(e)} value = {skills} name = 'skills' type = 'text'  />
          <h3>My Contacts</h3>
                <h4>Phone number:</h4>
                <input onChange = {e => setStateValues(e)} value = {phone_number} placeholder="888-888-8888" pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}" name = 'phone_number'  type = 'tel' />
          <h3>My Location</h3>
                <h4>State:</h4>
                <States style = {{marginBottom:"20px"}} onChange = {e => setStateValues(e)} name = 'state'  value = {state} />
                <h4>City:</h4>
                <input value = {city} onChange = {e => setStateValues(e)} name = 'city' type = 'text' />
          <h3>Availability</h3>

                <h4>I hire employees:</h4>
                <label className = {stl.radioChoice} htmlFor = 'hiring-yes'>
                   <input id = 'hiring-yes' onChange = {e => setStateValues(e)}  name = 'hiring' type = 'radio' value = 'yes' checked = {hiring} />yes
                </label>
                <label className = {stl.radioChoice} htmlFor = 'hiring-no'>
                   <input id = 'hiring-no' onChange = {e => setStateValues(e)} name = 'hiring' type = 'radio' value = 'no' checked = {!hiring} />no
                </label>

                <h4>I am hirable:</h4>
                <label className = {stl.radioChoice} htmlFor = 'hirable-yes'>
                   <input id = 'hirable-yes' onChange = {e => setStateValues(e)}  name = 'working' type = 'radio' value = 'yes' checked = {working} />yes
                </label>
                <label className = {stl.radioChoice} htmlFor = 'hirable-no'>
                   <input id = 'hirable-no' onChange = {e => setStateValues(e)} name = 'working' type = 'radio' value = 'no' checked = {!working} />no
                </label>

                <h4>My profile searchable for Employers:</h4>
                <label className = {stl.radioChoice} htmlFor = 'available-yes'>
                   <input id = 'available-yes' onChange = {e => setStateValues(e)}  name = 'available' type = 'radio' value = 'yes' checked = {available} />yes
                </label>
                <label className = {stl.radioChoice} htmlFor = 'available-no'>
                   <input id = 'available-no' onChange = {e => setStateValues(e)} name = 'available' type = 'radio' value = 'no' checked = {!available} />no
                </label>
                </div>
        <button type = 'submit'>SUBMIT</button>
        </form>

     </div>
     </div>
    )
})

const EditProfile = () => (
    <Layout>
        <SEO title = 'profile' />
        <EditProfileBody />
    </Layout>
)

export default EditProfile